body {
  margin: 0;
  padding: 0;
  background-color: white;
  font-family: sans-serif;
  font-size: 16px;
  line-height: 1.4;
  color: #333;
}

/*layout*/
/*top-header*/
.container {
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

@media (min-width: 992px) {
  .container {
    width: 970px;
  }
}
.row {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
}
.App {
  text-align: center;
}

/* Properties */

#dhead {
  margin-top: 20px;
}

#dpic {
  grid-column: span 6;
  text-align: right;
}
#ddesc {
  padding-top: 40px;
  padding-left: 20px;
  grid-column: span 6;
  text-align: left;
}
@media (max-width: 991px) {
  #dpic {
    grid-column: span 12;
    text-align: center;
  }
  #ddesc {
    grid-column: span 12;
    padding-top: 20px;
    text-align: center;
  }
}
h1 {
  font-size: 34px;
  font-weight: normal;
  padding: 0;
  margin: 0;
}
h2 {
  font-weight: normal;
  font-style: italic;
  color: #999;
  font-size: 18px;
  padding: 0;
  margin: 5px 0 10px 0;
}
@media (min-width: 992px) {
  h2 {
    max-width: 300px;
  }
}
#dpic img {
  width: 240px;
  height: 240px;
  border-radius: 120px;
}
.iico {
  width: 40px;
  height: 40px;
  padding-right: 3.5px;
}

/*-------------------------------------------------------------------------------------------*/
/*--------------------------history---------------------------------------------f*/

#history {
  margin-top: 20px;
}
#history a {
  color: blue;
}
.timespan {
  grid-column: span 1;
  font-size: 14px;
  text-align: right;
  padding-right: 5px;
  color: #bbb;
}
.ico {
  grid-column: span 1;
  vertical-align: top;
  border-left: 2px solid #cfcfcf;
  position: relative;
}
.ico img {
  border-radius: 5px;
  width: 100%;
  max-width: 80px;
  margin-left: 10px;
}

.desc {
  grid-column: span 10;
  vertical-align: top;
  font-size: 17px;
  padding-left: 20px;
  padding-bottom: 20px;
  text-align: left;
}

.entry-dot {
  position: absolute;
  top: 0px;
  left: -8px;
  width: 10px;
  height: 10px;
  border-radius: 7px;
  background-color: #cfcfcf;
  border: 2px solid white;
}

/*______________________________ Navbar ____________________________________________ */
.navbar {
  display: none; /* Initially hidden */
  position: fixed; /* Fixed positioning */
  top: 0; /* At the top of the viewport */
  width: 100%; /* Full width */
  background-color: black; /* Background color */
  color: white; /* Text color */
  padding: 10px 0 2px 10px;
  transition: opacity 0.3s ease, transform 0.3s ease; /* Smooth transition for appearance */
  z-index: 1000; /* Ensure it is above other content */
}

/* When navbar is visible */
.navbar.visible {
  display: flex; /* Use flex to maintain your layout */
  align-items: center; /* Align items vertically */
  justify-content: space-between; /* Space out items */
  opacity: 1; /* Fully opaque when visible */
  transform: translateY(
    0
  ); /* Reset any translateY to ensure it appears correctly */
}

/* Optional: When not visible, you might want to add this for a smoother experience */
.navbar:not(.visible) {
  opacity: 0; /* Fully transparent when not visible */
  transform: translateY(-100%); /* Slide up out of view */
}

.navbar-brand {
  display: flex;
  align-items: center;
}

.navbar-brand img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  margin-right: 15px;
}
.name {
  font-size: 1.5rem;
}
.navbar-links {
  display: flex;
}
.navbar-links ul {
  display: flex;
  list-style: none;
}
.navbar-links ul li {
  margin-left: 20px; /* Adds space between the links */
}
.navbar-links a {
  color: white;
  text-decoration: none;
  padding: 14px 20px;
}

/* Smooth scrolling */
html {
  scroll-behavior: smooth;
  scroll-padding-top: 80px;
}

/* Styling for new sections */
main {
  padding: 50px 20px;
}

section h2 {
  font-size: 2rem;
  margin-bottom: 20px;
}
.section {
  position: relative; /* Allows for absolute positioning of the square */
  padding: 20px; /* Adds some padding inside the section */
  margin: 20px; /* Adds space around the section */
}

.debug-square {
  position: absolute; /* Positions the square relative to the section */
  top: 0; /* Aligns to the top */
  left: 0; /* Aligns to the left */
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  background-color: rgba(0, 0, 255, 0.1); /* Light blue background */
  z-index: -1; /* Places the square behind the content */
}
/*___________________________ Project _________________________________________________________*/
.project {
  margin-bottom: 10px;
  padding-bottom: 10px;
  border-bottom: 1px solid #eee;
}
#pet-projects .row {
  grid-column-gap: 10px;
  grid-row-gap: 10px;
}
.project {
  margin-bottom: 10px;
  padding-bottom: 10px;
  border-bottom: 1px solid #eee;
}
.pico {
  float: left;
  margin-right: 10px;
}
.pdesc {
  white-space: normal; /* Allows text to wrap normally */
  word-wrap: break-word; /* Ensures long words break and wrap to the next line */
  text-align: left;
}
.pend {
  clear: both;
}
.pico img {
  height: 80px;
  border-radius: 5px;
  border: 1px solid #999;
}
.pub {
  font-size: 14px;
  border-left: 4px solid #aaa;
  padding: 2px 0px 2px 10px;
  margin-bottom: 10px;
}
.pub-title {
  display: inline;
  color: #333;
}
.pub-venue {
  display: inline;
  color: #090;
}
.pub-authors {
  display: block;
  color: #900;
}
.tul {
  text-align: center;
  padding: 0;
}
.til {
  display: inline-block;
  padding: 3px 15px 3px 15px;
  margin-bottom: 5px;
}
.tilb {
  border-right: 1px solid #ccc;
}
.nodot {
  list-style-type: none;
  padding-left: 0;
}
/*___________________________ Certificates _________________________________________________________*/

.ctitle {
  font-size: 36px;
  margin-bottom: 20px;
  margin-top: 40px;
}
#featured-talks .row {
  grid-column-gap: 10px;
  grid-row-gap: 10px;
  padding-bottom: 30px;
}
.row {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
}
.card {
  grid-column: span 3;
  vertical-align: top;
  background-color: #eee;
  border: 1px solid #ddd;
  border-radius: 3px;
  padding: 5px;
  background-color: white;
  padding-bottom: 10px;
}
.card img {
  border-radius: 5px;
  width: 100%;
}

/*___________________________ Youtube _________________________________________________________*/

#Youtube {
  padding: 40px 20px; 
  background-color: #f9f9f9; 
}

.ctitle {
  font-size: 36px;
  margin-bottom: 20px;
  margin-top: 0; 
  text-align: center;
}

.youtube-container {
  display: grid;
  grid-template-columns: repeat(
    auto-fill,
    minmax(300px, 1fr)
  ); 
  grid-gap: 20px; 
}

.youtube-container iframe {
  width: 100%; 
  height: auto;
  border-radius: 5px; 
}